import React, { Component } from "react";
import { Grid, Input, Button, Segment } from "semantic-ui-react";
import Modal from "./modal";

export default class Sum extends Component {
  constructor(props) {
    super(props);
    this.state = {
      inputNum: 0,
      sum: 0,
      error: ""
    };
  }

  onChange(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
  }

  handleSum() {
    const { inputNum } = this.state;
    this.setState(s => ({
      sum: s.sum + +inputNum
    }));
  }

  randomNum() {
    return Math.floor(Math.random() * 20) + 1;
  }

  render() {
    const { inputNum, sum, error } = this.state;
    const style = {
      verticalAlign: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center"
      },
      showBox: {
        fontSize: "3rem",
        padding: "0.5em 0",
        overflow: "auto",
        textAlign: "center"
      }
    };

    return (
      <Grid
        columns={3}
        stackable
        container
        centered
        style={{ ...style.verticalAlign, height: "100vh" }}
      >
        {error && (
          <Modal
            text={error}
            handleClick={() => this.setState({ error: "" })}
          />
        )}

        <Grid.Row>
          <Grid.Column stretched style={{ width: "180px" }}>
            <Input
              style={{ marginBottom: "0.25em" }}
              fluid
              type="number"
              name="inputNum"
              id="inputNum"
              value={inputNum}
              onChange={e => this.onChange(e)}
              input={{ style: style.showBox }}
            />
          </Grid.Column>
          <Grid.Column
            stretched
            style={{ ...style.verticalAlign, width: "180px" }}
          >
            <div
              style={{
                ...style.verticalAlign,
                justifyContent: "space-between"
              }}
            >
              <Button
                positive
                fluid
                content="Add To Sum"
                style={{ fontSize: "1.25rem" }}
                onClick={() => this.handleSum()}
              />
              <Button
                positive
                fluid
                content="Random"
                style={{ fontSize: "1.25rem", margin: "0.25rem 0" }}
                onClick={() => this.setState({ inputNum: this.randomNum() })}
              />
              <Button
                positive
                fluid
                content="Reset"
                style={{ fontSize: "1.25rem" }}
                onClick={() => this.setState({ sum: 0, inputNum: 0 })}
              />
            </div>
          </Grid.Column>
          <Grid.Column stretched style={{ width: "180px" }}>
            <Segment style={{ ...style.verticalAlign, ...style.showBox }}>
              {sum}
            </Segment>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    );
  }
}
