import React from "react";
import { Button, Modal } from "semantic-ui-react";

export default props => (
  <Modal open size='mini'>
    <Modal.Header>
      <p style={{textAlign:'center'}}>{props.text}</p>
    </Modal.Header>
    <Modal.Actions>
      <Button onClick={props.handleClick}>Ok</Button>
    </Modal.Actions>
  </Modal>
);