import React, { Component } from "react";
import { Grid, Input, Button, Segment } from "semantic-ui-react";
import Modal from "./modal";

export default class Calc extends Component {
  constructor(props) {
    super(props);
    this.state = {
      numberA: 0,
      numberB: 0,
      result: 0,
      error: ""
    };
  }

  onChange(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
  }

  onCalculate(op) {
    const { numberA, numberB } = this.state;
    let result = eval(`${+numberA} ${op} ${+numberB}`);
    if(!Number.isInteger(result)) result = result.toFixed(2);

    if (result === Infinity || isNaN(result)) {
      return this.setState({
        error: "Cannot divide by 0"
      });
    }

    this.setState({ result });
  }

  getMax() {
    const { numberA, numberB } = this.state;
    let error = "";
    if (numberA > numberB) error = `A = ${numberA} is bigger`;
    else if (numberA < numberB) error = `B = ${numberB} is bigger`;
    else error = `A = B = ${numberA}`;
    this.setState({ error });
  }

  randomNum(){
    return Math.floor(Math.random()*20) +1;
  };

  render() {
    const { numberA, numberB, result, error } = this.state;
    const style = {
      verticalAlign: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center"
      },
      showBox: {
        fontSize: "3rem",
        padding: "0.5em 0",
        overflow: "auto",
        textAlign: "center"
      }
    };

    return (
      <Grid
        columns={3}
        stackable
        container
        centered
        style={{ ...style.verticalAlign, height: "100vh" }}
      >
        {error && (
          <Modal
            text={error}
            handleClick={() => this.setState({ error: "" })}
          />
        )}

        <Grid.Row>
          <Grid.Column stretched style={{ width: "180px" }}>
            <Input
              style={{ marginBottom: "0.25em" }}
              fluid
              type="number"
              name="numberA"
              value={numberA}
              onChange={e => this.onChange(e)}
              input={{style:{textAlign: 'center'}}}
              label={{ basic: true, content: "A", style:style.verticalAlign}}
              action={{
                icon:'refresh',
                onClick:()=>{this.setState({numberA:this.randomNum()})}
              }}
            />
            <Input
              fluid
              type="number"
              name="numberB"
              value={numberB}
              onChange={e => this.onChange(e)}
              input={{style:{textAlign: 'center'}}}
              label={{ basic: true, content: "B", style:style.verticalAlign}}
              action={{
                icon:'refresh',
                onClick:()=>{this.setState({numberB:this.randomNum()})}
              }}
            />
          </Grid.Column>
          <Grid.Column style={{ ...style.verticalAlign, width: "150px" }}>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                marginBottom: "0.25em"
              }}
            >
              <Button
                fluid
                circular
                content="+"
                onClick={() => this.onCalculate("+")}
              />
              <Button
                fluid
                circular
                content="-"
                onClick={() => this.onCalculate("-")}
              />
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                marginBottom: "0.25em"
              }}
            >
              <Button
                fluid
                circular
                content="×"
                onClick={() => this.onCalculate("*")}
              />
              <Button
                fluid
                circular
                content="÷"
                onClick={() => this.onCalculate("/")}
              />
            </div>
            <div style={{ display: "flex", justifyContent: "center" }}>
              <Button
                fluid
                circular
                content="max"
                onClick={() => this.getMax()}
              />
            </div>
          </Grid.Column>
          <Grid.Column stretched>
            <Segment
              id="show"
              style={{ ...style.verticalAlign, ...style.showBox }}
            >
              {result}
            </Segment>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    );
  }
}
