import React, { Component } from "react";
import { Grid, Input, Button, Segment } from "semantic-ui-react";

export default class Calc extends Component {
  constructor(props) {
    super(props);
    this.state = {
      firstName: "Gregory",
      lastName: "Smirnov",
      birthday: "1988-08-09",
      message: ""
    };
  }

  onChange(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
  }

  formatDate(dateString) {
    const date = new Date(dateString);
    const months = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December"
    ];
    debugger;
    return {
      day: date.getDate(),
      month: months[date.getMonth()],
      year: date.getFullYear()
    };
  }

  onSubmit() {
    this.setState(function(s) {
      const d = this.formatDate(s.birthday);
      return {
        message: `Hi, my name is ${s.firstName} ${s.lastName}, and I was born on ${d.month} ${d.day}, ${d.year}`
      };
    });
  }

  render() {
    const { firstName, lastName, birthday, message } = this.state;
    const style = {
      verticalAlign: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        lineHeight: "1.5"
      },
      showBox: {
        fontSize: "1.5rem",
        padding: "0.25em 0.25",
        overflowY: "auto",
        textAlign: "center"
      }
    };

    return (
      <Grid
        columns={3}
        stackable
        container
        centered
        style={{ ...style.verticalAlign, height: "100vh" }}
      >
        <Grid.Row>
          <Grid.Column>
            <Input
              style={{ marginBottom: "0.25em" }}
              fluid
              type="text"
              name="firstName"
              value={firstName}
              onChange={e => this.onChange(e)}
              label={{ basic: true, content: "First Name", style:{width:'100px'}}}
            />
            <Input
              style={{ marginBottom: "0.25em" }}
              fluid
              type="text"
              name="lastName"
              value={lastName}
              onChange={e => this.onChange(e)}
              label={{ basic: true, content: "Last Name", style:{width:'100px'} }}
            />
            <Input
              fluid
              type="date"
              name="birthday"
              id="birthday"
              value={birthday}
              onChange={e => this.onChange(e)}
              label={{ basic: true, content: "Birthday", style:{width:'100px'} }}
            />
          </Grid.Column>
          <Grid.Column style={{ ...style.verticalAlign, width: "150px" }}>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                marginBottom: "0.25em"
              }}
            >
              <Button
                circular
                color="blue"
                size="huge"
                content="Submit"
                onClick={() => this.onSubmit()}
              />
            </div>
          </Grid.Column>
          <Grid.Column stretched>
            <Segment
              id="show"
              style={{ ...style.verticalAlign, ...style.showBox }}
            >
              {message}
            </Segment>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    );
  }
}
