import React, { Component } from "react";
import "./App.css";
import Calc from "../components/calc";
import Info from "../components/info";
import Sum from "../components/sum";
import { Tab } from "semantic-ui-react";

const panes = [
  { menuItem: "Calculator", render: () => <Calc /> },
  { menuItem: "Info Display", render: () => <Info /> },
  { menuItem: "Sum Calculator", render: () => <Sum /> }
];

class App extends Component {
  render() {
    return (
      <Tab
        panes={panes}
        menu={{
          widths: 3,
          size: "huge",
          fixed: "top",
          color: "brown",
          inverted: 1
        }}
      />
    );
  }
}

export default App;
